.button {
    background-color: transparent;
    border: 1px solid #2d2d2d;
    border-radius: 8px;
    padding: 15px 30px;
    font-size: 18px;
    cursor: pointer;
    transition: all .2s;
    display: inline-block;
}
.button:hover {
    background-color: #0082FA;
    border: 1px solid #0082FA;
    color: #fafafa;
}