@media only screen and (max-width: 1199px) {
  .home-container h1 {
      font-size: 45px;
      line-height: 55px;
      margin-bottom: 20px;
  }
  p {
      font-size: 18px;
      line-height: 28px;
  }
  .home-container .button {
      padding: 18px 40px;
  }
}
@media only screen and (max-width: 992px) {

  .nave-link-menu {
      position: fixed;
      background-color: #fafafa;
      width: 100%;
      height: 100%;
      z-index: 8;
      left: -6000px;
      top: 0px;
      transition: all .2s;
  }
  .nave-link-menu .material-icons {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 40px;
  }
  .show .nave-link-menu {
    left: 0px;
  }
  .hamb-menu {
    display: block;
    margin-left: 15px;
  }
  .nave-link ul {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100vh;
      width: 100%;
  }
  .nave-link ul li {
      margin-left: 0;
      margin-bottom: 35px;
      font-size: 28px;
  }
  .home-banner img {
    display: none;
  }
  .home-text {
    max-width: inherit;
    text-align: center;
    width: 100%;
  }
  .navbar {
    padding-top: 15px;
  }
  .home-container {
    padding: 0px 15px;
    display: flex;
    height: calc(100vh - 170px);
    align-items: center;
    justify-content: center;
  }
  .por-wrapp .por-wrapp-col {
    padding: 15px;
  }
  
}
@media only screen and (max-width: 768px) {
  .por-wrapp .por-wrapp-col {
    max-width: 405px;
    flex: auto;
    margin: 0px 0px 15px;
  }
  
}
@media only screen and (max-width: 575px) {

  .home-container h1 {
    font-size: 28px;
    line-height: 38px;
  }
  p {
    font-size: 15px;
    line-height: 25px;
  }
  .navbar {
    justify-content: space-between;
  }
  .navbar button {
    display: none;
  } 
  .footer-home {
    flex-direction: column;
  }
  .send-query {
    display: none;
  }
  .exp-wrapp {
    display: block;
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #ececec;
  }
  .exp-year {
    max-width: inherit;
    border: none;
    margin-bottom: 25px;
  }
  .navbar .button {
    display: none;
  }
}